@import './variables/vars';

@font-face {
  font-family: 'Lato';
  src: url('../../assets/fonts/Lato-Regular.woff'), url('../../assets/fonts/Lato-Regular.woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Lato';
  src: url('../../assets/fonts/Lato-Semibold.woff'), url('../../assets/fonts/Lato-Semibold.woff2');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Raleway';
  src: url('../../assets/fonts/Raleway-Regular.ttf');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Raleway';
  src: url('../../assets/fonts/Raleway-Bold.ttf');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Raleway';
  src: url('../../assets/fonts/Raleway-Black.ttf');
  font-weight: 900;
  font-style: normal;
}

html,
body {
  background-color: $mainBackColor;
  min-height: 100%;
  max-width: 1920px;
  min-width: 360px;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Lato', sans-serif;
  overflow-x: hidden;
  box-sizing: border-box;
  color: white;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}

div {
  box-sizing: border-box;
}

fieldset {
  border: none;
  padding: 0;
}

.error {
  color: red;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;

  &:focus {
    outline: none;
    border: none;
  }
}

p {
  margin: 0;
  padding: 0;
}

input {
  &:focus,
  &:active {
    outline: none;
  }
}

button,
input {
  cursor: pointer;
}

.main-container {
  max-width: 1280px;
  width: calc(100% - 20px);
  padding: 0 10px;
  margin: 0 auto;
}
